.project-intro {
  border: 4px solid rgb(231, 163, 4);
  border-radius: 10px;
  margin-top: 3rem;
}

.movie-img img {
  max-width: 500px;
  height: 300px;
  margin: 20px;
}

.project-intro {
  display: flex;
}

.project-text {
  margin-left: 5rem;
  margin-top: 20px;

}

.project-heading {
  font-weight: bold;
  font-size: 1.3rem;
}

.project-dec {
  font-weight: bold;
  font-size: 1rem;
  margin-top: 1.2rem;
}

.project-content {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  margin-left: 4rem;
  margin-top: 0.8rem;
  font-size: 1rem;
}

.project-content span {
  font-weight: bold;
}

.project-tech {
  margin-top: 0.8rem;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.project-tech span {
  font-weight: bold;

}

@media (max-width: 1230px) {

  .container {
    width: 1000px;
  }

  .movie-img img {
    max-width: 500px;
    height: 310px;
  }

  .project-text {
    margin-left: 3rem;
  }

  .project-content {
    margin-left: 1rem;
  }
}

@media (max-width: 1030px) {
  .container {
    width: 800px;
  }

  .project-intro {
    display: flex;
    flex-direction: column;
  }

  .movie-img img {
    max-width: 800px;
    height: 360px;
  }

  .project-text {
    margin-bottom: 1rem;
  }
}

